import * as React from "react";
import MarkdownIt from "markdown-it";
import MdEditor from "react-markdown-editor-lite";
import MarkdownItTexMath from "markdown-it-texmath";
import MarkDownItSup from "markdown-it-sup";
import MarkItContainer from "markdown-it-container"

// import style manually
import "react-markdown-editor-lite/lib/index.css";
import './editor.css';

/**
 * @author
 * @function ReviewEditor
 **/

const ProjectPageEditor = ({
  textValue,
  setTextValue
}) => {

  const mdParser = new MarkdownIt({html:true});
  mdParser.use(MarkdownItTexMath)
  mdParser.use(MarkDownItSup)
  mdParser.use(MarkItContainer, "author")
  mdParser.use(MarkItContainer, "institution")
  mdParser.use(MarkItContainer, "button")
  mdParser.use(MarkItContainer, "caption")
  // Finish!
  function handleEditorChange({ html, text }) {
    setTextValue(text);
  }

  return (
    <>
      <form className="editor-box">
        <MdEditor
          value={textValue}
          placeholder="Write a project page... (Markdown &#10003;)"
          renderHTML={(text) => mdParser.render(text)}
          onChange={handleEditorChange}
        />
      </form>
    </>
  );
};

export default ProjectPageEditor;
